import React, { useState, useEffect, useRef, Component } from 'react';
import { Circle } from 'react-konva';

// Scaling Constants for Canvas
const SCALE = 0.05;
const OFFSET = 80;
export const canvasWidth = window.innerWidth;
export const canvasHeight = window.innerHeight;

function isIntersect(point, el) {
    return Math.sqrt((point.x-el.x) ** .5 + (point.y - el.y) ** .5) < 10;
}

export class Contract extends React.Component {
    constructor(props) {
        super(props);
        this.state = { isMouseInside: false};
        this.handleMouseEnter = this.handleMouseEnter.bind(this);
        this.handleMouseLeave = this.handleMouseLeave.bind(this);
    }
    handleMouseEnter() {
        this.setState({ isMouseInside: true});
    }
    handleMouseLeave() {
        this.setState({ isMouseInside: false});
    }
    render() {
        return (
            <Circle
                x={100} y={60} radius={50}
                fill="yellow" stroke="black"
                strokeWidth={this.state.isMouseInside ? 5 : 1}
                onMouseEnter={this.handleMouseEnter}
                onMouseLeave={this.handleMouseLeave}
            />
        );
    }
}